'use client';

import { useState, useEffect, useCallback } from 'react';
import { useSearchParams, useRouter } from 'next/navigation';
import Image from 'next/image';
import Link from 'next/link';
import { Search, Filter, Star, ShoppingCart, Grid, List } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import { useCart } from '@/hooks/useCart';

interface Product {
  id: number;
  name: string;
  price: number;
  originalPrice?: number;
  image: string;
  rating: number;
  reviews: number;
  category: string;
  inStock: boolean;
  description: string;
  tags: string[];
}

const categories = [
  'Все категории',
  'Воздушные шары',
  'Детские праздники',
  'Свадебные украшения',
  'Праздничная посуда',
  'Подарки и сувениры'
];

const sortOptions = [
  { value: 'popular', label: 'По популярности' },
  { value: 'price-asc', label: 'По цене: по возрастанию' },
  { value: 'price-desc', label: 'По цене: по убыванию' },
  { value: 'rating', label: 'По рейтингу' },
  { value: 'newest', label: 'Новинки' }
];

// Моковые данные на случай ошибки API
const mockProducts: Product[] = [
  {
    id: 1,
    name: 'Набор воздушных шаров "Радуга"',
    price: 1299,
    originalPrice: 1599,
    image: '/images/products/balloon.png',
    rating: 4.8,
    reviews: 124,
    category: 'Воздушные шары',
    inStock: true,
    description: 'Яркий набор воздушных шаров в цветах радуги. Идеально подходит для детских праздников и дней рождения.',
    tags: ['воздушные шары', 'детский праздник', 'радуга', 'день рождения']
  },
  {
    id: 2,
    name: 'Декор для дня рождения "Принцесса"',
    price: 2499,
    image: '/images/products/balloon.png',
    rating: 4.9,
    reviews: 89,
    category: 'Детские праздники',
    inStock: true,
    description: 'Полный набор декораций для создания сказочного праздника маленькой принцессы.',
    tags: ['принцесса', 'детский праздник', 'декор', 'розовый']
  },
  {
    id: 3,
    name: 'Свадебная арка с цветами',
    price: 8999,
    originalPrice: 10999,
    image: '/images/products/balloon.png',
    rating: 5.0,
    reviews: 56,
    category: 'Свадебные украшения',
    inStock: true,
    description: 'Элегантная свадебная арка с живыми цветами для создания романтической атмосферы.',
    tags: ['свадьба', 'арка', 'цветы', 'декор']
  },
  {
    id: 4,
    name: 'Набор праздничной посуды золотой',
    price: 899,
    image: '/images/products/balloon.png',
    rating: 4.7,
    reviews: 203,
    category: 'Праздничная посуда',
    inStock: true,
    description: 'Элегантный набор одноразовой посуды в золотом цвете для торжественных мероприятий.',
    tags: ['посуда', 'золотой', 'праздник', 'элегантность']
  }
];

export default function CatalogClientAnimated() {
  const [products, setProducts] = useState<Product[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('Все категории');
  const [sortBy, setSortBy] = useState('popular');
  const [viewMode, setViewMode] = useState<'grid' | 'list'>('grid');
  const [showFilters, setShowFilters] = useState(false);
  
  const searchParams = useSearchParams();
  const router = useRouter();
  const { addItem } = useCart();

  const fetchProducts = useCallback(async () => {
    setLoading(true);
    try {
      const params = new URLSearchParams();
      if (selectedCategory !== 'Все категории') {
        params.append('category', selectedCategory);
      }
      if (searchQuery) {
        params.append('search', searchQuery);
      }
      
      const response = await fetch(`/api/products?${params.toString()}`);
      if (!response.ok) throw new Error('Failed to fetch products');
      
      const data = await response.json();
      setProducts(data.data || mockProducts);
    } catch (error) {
      console.error('Error fetching products:', error);
      // Используем моковые данные в случае ошибки
      setProducts(mockProducts);
    } finally {
      setLoading(false);
    }
  }, [selectedCategory, searchQuery]);

  useEffect(() => {
    fetchProducts();
  }, [fetchProducts]);

  useEffect(() => {
    const category = searchParams?.get('category');
    const search = searchParams?.get('search');
    
    if (category) {
      setSelectedCategory(category);
    }
    if (search) {
      setSearchQuery(search);
    }
  }, [searchParams]);

  const handleSearch = (e: React.FormEvent) => {
    e.preventDefault();
    updateURL();
  };

  const updateURL = () => {
    const params = new URLSearchParams();
    if (selectedCategory !== 'Все категории') {
      params.append('category', selectedCategory);
    }
    if (searchQuery) {
      params.append('search', searchQuery);
    }
    
    const newURL = params.toString() ? `/catalog?${params.toString()}` : '/catalog';
    router.push(newURL);
  };

  const handleAddToCart = (product: Product) => {
    addItem({
      id: product.id,
      name: product.name,
      price: product.price,
      image: product.image
    });
  };

  const formatPrice = (price: number) => {
    return new Intl.NumberFormat('ru-RU', {
      style: 'currency',
      currency: 'RUB',
      minimumFractionDigits: 0
    }).format(price);
  };

  const sortedProducts = [...products].sort((a, b) => {
    switch (sortBy) {
      case 'price-asc':
        return a.price - b.price;
      case 'price-desc':
        return b.price - a.price;
      case 'rating':
        return b.rating - a.rating;
      default:
        return 0;
    }
  });

  // Анимационные варианты
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.05
      }
    }
  };

  const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: { type: "spring", stiffness: 300, damping: 24 }
    }
  };

  return (
    <div className="grid grid-cols-1 lg:grid-cols-4 gap-8">
      {/* Filters Sidebar */}
      <motion.div 
        initial={{ x: -20, opacity: 0 }}
        animate={{ x: 0, opacity: 1 }}
        transition={{ duration: 0.5 }}
        className={`lg:col-span-1 ${showFilters ? 'block' : 'hidden lg:block'}`}
      >
        <motion.div 
          className="bg-white rounded-xl shadow-sm p-6 sticky top-24"
          whileHover={{ boxShadow: "0 8px 30px rgba(0, 0, 0, 0.1)" }}
          transition={{ duration: 0.3 }}
        >
          <h3 className="text-lg font-semibold mb-4">Фильтры</h3>
          
          {/* Categories */}
          <div className="mb-6">
            <h4 className="font-medium mb-3">Категории</h4>
            <motion.div 
              className="space-y-2"
              variants={containerVariants}
              initial="hidden"
              animate="visible"
            >
              {categories.map((category, index) => (
                <motion.button
                  key={category}
                  variants={itemVariants}
                  onClick={() => {
                    setSelectedCategory(category);
                    updateURL();
                  }}
                  className={`block w-full text-left px-3 py-2 rounded-lg transition-colors ${
                    selectedCategory === category
                      ? 'bg-pink-100 text-pink-700'
                      : 'text-gray-600 hover:bg-gray-100'
                  }`}
                  whileHover={{ x: 5 }}
                  transition={{ type: "spring", stiffness: 400, damping: 10 }}
                >
                  {category}
                </motion.button>
              ))}
            </motion.div>
          </div>

          {/* Price Range */}
          <div className="mb-6">
            <h4 className="font-medium mb-3">Цена</h4>
            <div className="space-y-2">
              <label className="flex items-center">
                <motion.input 
                  type="checkbox" 
                  className="mr-2" 
                  whileTap={{ scale: 1.2 }}
                />
                <span className="text-sm">До 1000 ₽</span>
              </label>
              <label className="flex items-center">
                <motion.input 
                  type="checkbox" 
                  className="mr-2" 
                  whileTap={{ scale: 1.2 }}
                />
                <span className="text-sm">1000 - 5000 ₽</span>
              </label>
              <label className="flex items-center">
                <motion.input 
                  type="checkbox" 
                  className="mr-2" 
                  whileTap={{ scale: 1.2 }}
                />
                <span className="text-sm">5000 - 10000 ₽</span>
              </label>
              <label className="flex items-center">
                <motion.input 
                  type="checkbox" 
                  className="mr-2" 
                  whileTap={{ scale: 1.2 }}
                />
                <span className="text-sm">Свыше 10000 ₽</span>
              </label>
            </div>
          </div>

          {/* In Stock */}
          <div className="mb-6">
            <h4 className="font-medium mb-3">Наличие</h4>
            <label className="flex items-center">
              <motion.input 
                type="checkbox" 
                className="mr-2" 
                whileTap={{ scale: 1.2 }}
              />
              <span className="text-sm">Только в наличии</span>
            </label>
          </div>

          {/* Reset Filters */}
          <motion.button
            whileHover={{ scale: 1.03 }}
            whileTap={{ scale: 0.97 }}
            className="w-full py-2 bg-pink-500 text-white rounded-lg font-medium hover:bg-pink-600 transition-colors"
          >
            Сбросить фильтры
          </motion.button>
        </motion.div>
      </motion.div>

      {/* Products Grid */}
      <div className="lg:col-span-3">
        {/* Search and Sort */}
        <motion.div 
          className="flex flex-col sm:flex-row justify-between items-center mb-6 gap-4"
          initial={{ y: -20, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ duration: 0.5 }}
        >
          <form onSubmit={handleSearch} className="relative w-full sm:w-auto sm:flex-1">
            <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
              <Search className="h-5 w-5 text-gray-400" />
            </div>
            <motion.input
              type="text"
              className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-pink-500 focus:border-pink-500 outline-none"
              placeholder="Поиск товаров..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              whileFocus={{ scale: 1.01 }}
              transition={{ type: "spring", stiffness: 300, damping: 10 }}
            />
          </form>

          <div className="flex items-center gap-4 w-full sm:w-auto">
            <div className="hidden sm:flex">
              <motion.button
                whileHover={{ scale: 1.1 }}
                whileTap={{ scale: 0.9 }}
                onClick={() => setViewMode('grid')}
                className={`p-2 rounded-l-lg ${
                  viewMode === 'grid'
                    ? 'bg-pink-500 text-white'
                    : 'bg-gray-200 text-gray-600'
                }`}
              >
                <Grid className="h-5 w-5" />
              </motion.button>
              <motion.button
                whileHover={{ scale: 1.1 }}
                whileTap={{ scale: 0.9 }}
                onClick={() => setViewMode('list')}
                className={`p-2 rounded-r-lg ${
                  viewMode === 'list'
                    ? 'bg-pink-500 text-white'
                    : 'bg-gray-200 text-gray-600'
                }`}
              >
                <List className="h-5 w-5" />
              </motion.button>
            </div>

            <div className="w-full sm:w-auto">
              <motion.select
                value={sortBy}
                onChange={(e) => setSortBy(e.target.value)}
                className="block w-full sm:w-auto bg-white border border-gray-300 rounded-lg px-4 py-2 focus:ring-2 focus:ring-pink-500 focus:border-pink-500 outline-none"
                whileFocus={{ scale: 1.01 }}
                transition={{ type: "spring", stiffness: 300, damping: 10 }}
              >
                {sortOptions.map((option) => (
                  <option key={option.value} value={option.value}>
                    {option.label}
                  </option>
                ))}
              </motion.select>
            </div>

            <div className="sm:hidden">
              <motion.button
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
                onClick={() => setShowFilters(!showFilters)}
                className="flex items-center justify-center p-2 bg-pink-500 text-white rounded-lg"
              >
                <Filter className="h-5 w-5" />
              </motion.button>
            </div>
          </div>
        </motion.div>

        {/* Mobile Filters - Shown only on mobile when toggled */}
        <AnimatePresence>
          {showFilters && (
            <motion.div 
              className="sm:hidden mb-6"
              initial={{ height: 0, opacity: 0 }}
              animate={{ height: 'auto', opacity: 1 }}
              exit={{ height: 0, opacity: 0 }}
              transition={{ duration: 0.3 }}
            >
              <div className="bg-white rounded-xl shadow-sm p-4">
                {/* Mobile Filter Content - A simplified version of the sidebar */}
                <div className="mb-4">
                  <h4 className="font-medium mb-2">Категории</h4>
                  <div className="flex flex-wrap gap-2">
                    {categories.map((category) => (
                      <motion.button
                        key={category}
                        onClick={() => {
                          setSelectedCategory(category);
                          updateURL();
                        }}
                        className={`px-3 py-1 rounded-full text-sm ${
                          selectedCategory === category
                            ? 'bg-pink-500 text-white'
                            : 'bg-gray-100 text-gray-700'
                        }`}
                        whileHover={{ scale: 1.05 }}
                        whileTap={{ scale: 0.95 }}
                      >
                        {category}
                      </motion.button>
                    ))}
                  </div>
                </div>
              </div>
            </motion.div>
          )}
        </AnimatePresence>

        {/* Product Grid or List */}
        {loading ? (
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
            {[...Array(6)].map((_, i) => (
              <div key={i} className="bg-gray-100 animate-pulse h-96 rounded-lg" />
            ))}
          </div>
        ) : sortedProducts.length > 0 ? (
          <motion.div 
            className={
              viewMode === 'grid'
                ? 'grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6'
                : 'space-y-4'
            }
            variants={containerVariants}
            initial="hidden"
            animate="visible"
          >
            {sortedProducts.map((product) => (
              <motion.div
                key={product.id}
                variants={itemVariants}
                whileHover={{ y: -5 }}
                className={
                  viewMode === 'grid'
                    ? 'bg-white rounded-xl overflow-hidden shadow-sm group hover:shadow-md transition-shadow'
                    : 'bg-white rounded-xl p-4 shadow-sm flex items-center hover:shadow-md transition-shadow'
                }
              >
                {viewMode === 'grid' ? (
                  <Link href={`/products/${product.id}`}>
                    <div className="relative h-48 bg-gray-100 overflow-hidden">
                      <Image
                        src={product.image}
                        alt={product.name}
                        fill
                        sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
                        className="object-cover group-hover:scale-105 transition-transform duration-300"
                      />
                      {!product.inStock && (
                        <div className="absolute inset-0 bg-gray-900/70 flex items-center justify-center">
                          <span className="text-white font-medium px-4 py-2 rounded-full bg-gray-800">
                            Нет в наличии
                          </span>
                        </div>
                      )}
                    </div>
                    <div className="p-4">
                      <div className="flex items-center mb-2">
                        <div className="flex text-yellow-400">
                          {[...Array(5)].map((_, i) => (
                            <Star
                              key={i}
                              className={`h-4 w-4 ${
                                i < Math.floor(product.rating)
                                  ? 'text-yellow-400 fill-yellow-400'
                                  : 'text-gray-300'
                              }`}
                            />
                          ))}
                        </div>
                        <span className="text-xs text-gray-500 ml-2">({product.reviews})</span>
                      </div>
                      <h3 className="font-medium text-gray-900 group-hover:text-pink-500 transition-colors">
                        {product.name}
                      </h3>
                      <p className="text-sm text-gray-500 mb-2">{product.category}</p>
                      <div className="flex justify-between items-center mt-4">
                        <div>
                          {product.originalPrice && (
                            <span className="text-sm text-gray-500 line-through mr-2">
                              {formatPrice(product.originalPrice)}
                            </span>
                          )}
                          <span className="font-semibold text-gray-900">
                            {formatPrice(product.price)}
                          </span>
                        </div>
                        <motion.button
                          whileHover={{ scale: 1.1 }}
                          whileTap={{ scale: 0.9 }}
                          onClick={(e) => {
                            e.preventDefault();
                            e.stopPropagation();
                            handleAddToCart(product);
                          }}
                          className="p-2 bg-pink-500 text-white rounded-full hover:bg-pink-600 transition-colors"
                          disabled={!product.inStock}
                        >
                          <ShoppingCart className="h-5 w-5" />
                        </motion.button>
                      </div>
                    </div>
                  </Link>
                ) : (
                  <>
                    <div className="relative w-24 h-24 flex-shrink-0 mr-4 overflow-hidden rounded-lg">
                      <Image
                        src={product.image}
                        alt={product.name}
                        fill
                        sizes="(max-width: 768px) 100vw, 96px"
                        className="object-cover"
                      />
                    </div>
                    <div className="flex-1">
                      <Link href={`/products/${product.id}`}>
                        <h3 className="font-medium text-gray-900 hover:text-pink-500 transition-colors">
                          {product.name}
                        </h3>
                      </Link>
                      <p className="text-sm text-gray-500">{product.category}</p>
                      <div className="flex items-center mt-1 mb-2">
                        <div className="flex text-yellow-400">
                          {[...Array(5)].map((_, i) => (
                            <Star
                              key={i}
                              className={`h-3 w-3 ${
                                i < Math.floor(product.rating)
                                  ? 'text-yellow-400 fill-yellow-400'
                                  : 'text-gray-300'
                              }`}
                            />
                          ))}
                        </div>
                        <span className="text-xs text-gray-500 ml-2">({product.reviews})</span>
                      </div>
                      <div className="flex justify-between items-center">
                        <div>
                          {product.originalPrice && (
                            <span className="text-sm text-gray-500 line-through mr-2">
                              {formatPrice(product.originalPrice)}
                            </span>
                          )}
                          <span className="font-semibold text-gray-900">
                            {formatPrice(product.price)}
                          </span>
                        </div>
                        <motion.button
                          whileHover={{ scale: 1.1 }}
                          whileTap={{ scale: 0.9 }}
                          onClick={() => handleAddToCart(product)}
                          className="p-2 bg-pink-500 text-white rounded-full hover:bg-pink-600 transition-colors"
                          disabled={!product.inStock}
                        >
                          <ShoppingCart className="h-5 w-5" />
                        </motion.button>
                      </div>
                    </div>
                  </>
                )}
              </motion.div>
            ))}
          </motion.div>
        ) : (
          <motion.div 
            className="text-center py-12"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ duration: 0.5 }}
          >
            <p className="text-gray-500 text-lg">По вашему запросу ничего не найдено</p>
            <motion.button
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              onClick={() => {
                setSearchQuery('');
                setSelectedCategory('Все категории');
                router.push('/catalog');
              }}
              className="mt-4 px-4 py-2 bg-pink-500 text-white rounded-lg font-medium hover:bg-pink-600 transition-colors"
            >
              Сбросить фильтры
            </motion.button>
          </motion.div>
        )}

        {/* Pagination */}
        {!loading && sortedProducts.length > 0 && (
          <motion.div 
            className="flex justify-center mt-12"
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: 0.2 }}
          >
            <nav className="inline-flex rounded-md shadow-sm">
              <motion.button
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
                className="px-3 py-2 border border-gray-300 rounded-l-md bg-white text-gray-500 hover:bg-gray-50"
              >
                Назад
              </motion.button>
              <motion.button
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
                className="px-3 py-2 border-t border-b border-gray-300 bg-pink-500 text-white"
              >
                1
              </motion.button>
              <motion.button
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
                className="px-3 py-2 border-t border-b border-gray-300 bg-white text-gray-700 hover:bg-gray-50"
              >
                2
              </motion.button>
              <motion.button
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
                className="px-3 py-2 border-t border-b border-gray-300 bg-white text-gray-700 hover:bg-gray-50"
              >
                3
              </motion.button>
              <motion.button
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
                className="px-3 py-2 border border-gray-300 rounded-r-md bg-white text-gray-500 hover:bg-gray-50"
              >
                Вперед
              </motion.button>
            </nav>
          </motion.div>
        )}
      </div>
    </div>
  );
} 